import "./Logo.css"
import { Link } from 'react-router-dom'

const Logo = ({size, marginBottom}) => {
    let root = document.documentElement;
    root.style.setProperty('--logo-size', size ? size : 2);
    return (
        <Link to="/">
            <div className="logo" style={{marginBottom:`${marginBottom}rem`}}>
                <span className="material-icons icon">restaurant</span>
                <h1 className="title">Foodtionary</h1>
            </div>
        </Link>
    )
}

export default Logo
