import { Link } from 'react-router-dom'

import "./Recipe.css"
const Recipe = ({recipe, recipeID}) => {
    const calories = Math.floor(recipe.calories/recipe.yield);
    const handleImageLoad = (e) =>{
        e.target.style.borderColor = "rgba(63, 184, 63, 0.8)";
    }
    return (
        <>
        <Link to={{
                    pathname: "/"+recipeID,
                    state: recipe,
                }}>
            {<img src={recipe.image} alt="" className="recipe_image" onLoad={handleImageLoad}/>}
            <div className="recipe_details">
                
                    <h1>{recipe.label}</h1>
                <p>
                    Calories per serving: {calories}
                </p>
            </div>
        </Link>
        </>
    )
}

export default Recipe
