import React from 'react';
import { useLocation } from 'react-router';
import Logo from './Logo';
import "./RecipeDetails.css"
import Search from './Search';

const RecipeDetails = () => {
    const recipe = useLocation().state;
    const requiredNutrinets = ["Energy", "Fat", "Carbs", "Sodium", "Vitamin B6", "Vitamin B12", "Vitamin C", "Iron", "Zinc", "Magnesium"]
    const nutrients = Object.entries(recipe.totalNutrients);
    console.log(nutrients)
    return (
        <>
            <nav>
                <div className="container nav_container">
                    <Logo />
                    <Search/>
                </div>
            </nav>
            <div className="container ingredients_container">
                <h1 className="recipe_name">{recipe.label}</h1>
                <div className="flex">
                    <img src={recipe.image} alt="" className="mobile_img"/>
                    <div className="ingredients_wrapper">
                        <h1>Ingredients</h1>
                        <ul className="ingredients">
                            {recipe.ingredientLines.map((ing, i) => {
                                return <li key={i}>{ing}</li>
                            })}
                        </ul>
                        <div className="extra">
                            {recipe.totalTime !== 0 ? <div className="time">
                                <h3>Time:</h3>
                                <span>{recipe.totalTime} mins</span>
                            </div> : <></>}
                            <div className="servings">
                                <h3>Servings:</h3>
                                <span>{recipe.yield} serving(s)</span>
                            </div>
                        </div>
                    </div>
                    <div className="idk">
                        <img src={recipe.image} alt="" className="desktop_img"/>
                        <table>
                            <tbody>
                                <tr>
                                    <th>Nutrient</th>
                                    {nutrients.map(nutrient => {
                                        if(requiredNutrinets.includes(nutrient[1].label))
                                        return <td>{nutrient[1].label}</td>
                                    })}
                                </tr>
                                <tr>
                                    <th>Quantity</th>
                                    {nutrients.map(nutrient => {
                                        if(requiredNutrinets.includes(nutrient[1].label))
                                        return <td>{Math.floor(nutrient[1].quantity) + " " + nutrient[1].unit}</td>
                                    })}
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </>
    )
}

export default RecipeDetails
