import "./Home.css"
import Logo from "./Logo";
import Search from "./Search";
const Home = () => {
    return (
        <div className="home">
            <div className="glass">
                <div className="home_container">
                    <Logo size="5" marginBottom="1"/>
                    <h2 className="slogan">New and delicious recipes right at your fingertips</h2>
                    <Search/>
                </div>
            </div>
        </div>
    )
}

export default Home
