import React from 'react';
import Recipe from "./Recipe";

const RecipeList = ({recipes}) => {
    return (
        <ul className="recipe_list">
            {recipes.map((recipe, index) => {
                recipe = recipe.recipe;
                const uri = "http://www.edamam.com/ontologies/edamam.owl#recipe_"
                const recipeID = recipe.uri.substring(uri.length,);
                recipe.label = recipe.label.replace("recipes","");
                return (
                    <li key={recipeID} className="recipe">
                        <Recipe recipe={recipe} recipeID={recipeID} />
                    </li>
                )
            })}
        </ul>
    )
}

export default RecipeList
