import React, { useEffect } from 'react'


const useFetch = (searchText, setRecipes, setIsLoading) => {
    const APP_ID = "3601ca21";
    const APP_KEY = "f83814e75e3d0403ecd7712e0c50619d";
    const url = `https://api.edamam.com/api/recipes/v2?type=public&q=${searchText}&calories=1000-3000&app_id=${APP_ID}&app_key=%20${APP_KEY}`;

    const getData = async () => {
        const response = await fetch(url);
        const data = await response.json();
        setIsLoading(false)
        setRecipes(data.hits)
    }
    useEffect(() => {
        getData()
    }, [searchText])
}

export default useFetch